.logo {
    display: flex; 
    align-items: center;   
}

.logo__name {
    display: flex;
    justify-content: flex-start;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(30px, 34px, bold);

    & span:first-child {
        color: #FF9514;
    }

    & span:last-child {
        color: #111111;
    }
}

.logo__text {
    position: relative;
    margin-left: 35px;
    font-family: 'Gilroy', 'Arial', sans-serif;
    @include setFont(16px, 24px, normal);
    color: rgba(17, 17, 17, 0.5);
    letter-spacing: 0.92px;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -15px;
        height: 30px;
        width: 1px;
        background-color: #C4C4C4;
    }
}

@media (max-width: 660px) {
    .logo__name {
        & span:last-child {
            color: #ffffff;
        }
    }

    .logo__text {
        display: none;
    }
}
