.form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form__fields {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.form__field {
    position: relative;
    width: 312px;
    margin-right: 32px;
    background-color: #F3F3F4;
    border-radius: 16px;
    border: 1px solid transparent;

    & label {
        display: none;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 24px;
        @include setFont(16px, 24px, bold);
        color: #575757;
    }

    & input {
        width: 100%;
        padding: 12px 24px 12px 24px;
        background-color: transparent;
        border: none;
        outline: none;
        border-radius: 16px;
        @include setFont(16px, 24px, bold);
        color: #575757;

        &:focus {
            background-color: #ffffff;
        }
    }   
}

.form__field-message {
    display: none;
    position: absolute;
    bottom: -20px;
    left: 10px;
    color: #D53234;
    @include setFont(16px, 24px, normal);
}

.form__field--focus {
    border-color: rgba(255, 149, 20, 1);
    box-shadow: 0 0 0 4px rgba(255, 149, 20, 0.2);   

    & input {        
        box-sizing: border-box;
        border-radius: 16px;
        line-height: 16px;
        padding: 24px 24px 0 24px;
    }

    & label {
        display: block;
        top: 8px;
        transform: translateY(0);
        @include setFont(12px, 12px, normal);
    }
    
}

.form__field--invalid {
    border-color:#D53234;
}

.form__field--valid {
    &::before {
        position: absolute;
        content: "";
        bottom: 12px;
        right: 12px;
        width: 24px;
        height: 24px;
        background-color: #77DE8D;
        border-radius: 8px;
    }

    &::after {
        position: absolute;
        content: "";
        bottom: 22px;
        right: 18px;
        width: 10px;
        height: 5px;
        transform: rotate(-45deg);
        background-color: transparent;
        border: 2px solid #FFFFFF;
        border-top: none;
        border-right: none;
    }
}

.form__field--invalid .form__field-message {
    display: block;
}

@media (max-width: 768px) {
    .form__fields {
        flex-direction: column;
    }

    .form__field {
        width: 100%;
        margin-right: 0;
        margin-bottom: 16px;
    }
}