	font-family: 'Nekst';
	src: url("../fonts/nekst/Nekst-Regular.woff2") format("otf");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
	font-family: 'Nekst-Black';
	src: url("../fonts/nekst/Nekst-Black.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

@font-face {
    font-family: 'Nekst';
	src: url("../fonts/nekst/Nekst-Bold.woff2") format("woff2");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
