@font-face {
	font-family: 'Gilroy';
	src: url("../fonts/gilroy/Gilroy-Light.woff2") format("woff2");
	font-weight: 300;
	font-style: normal;
	font-display: swap;
}

@font-face {
    font-family: 'Gilroy';
	src: url("../fonts/gilroy/Gilroy-Bold.woff2") format("woff2");
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Gilroy';
	src: url("../fonts/gilroy/Gilroy-Regular.woff2") format("woff2");
    font-weight: 400;
    font-style: normal;
    font-display: swap;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

ul {
	list-style: none;
}

a {
	text-decoration: none;
}

body {
	min-width: 320px;
	font-family: 'Gilroy', 'Arial', sans-serif;
	padding-top: 95px;
	touch-action: none;
}

.visually-hidden {
	position: absolute;
	clip: rect(1px 1px 1px 1px);
	padding: 0;
	border: 0;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.wrapper,
.slide-wrapper {
	max-width: 1440px;
	min-width: 320px;
	padding: 0 48px;
	margin: 0 auto;
}

.button-orange {
	position: relative;
    padding: 12px 24px;
    background-color: #FF9514;
    border: none;
    color: #ffffff;
    border-radius: 40px;
    outline: none;
    @include setFont(16px, 24px, bold);
}

.button-orange:not(.button-orange--loading) {
	&:not(:disabled):hover {
        background-color: #111111;
        color: #ffffff;
    }

	&:not(:disabled):active {
		opacity: 0.6;
	}

	&:disabled {
		opacity: 0.3;
	}
}

.button-white {
	position: relative;
    padding: 12px 24px;
    background-color: #ffffff;
    border: 1px solid #FF9514;
    color: #FF9514;
    border-radius: 40px;
    outline: none;
    @include setFont(16px, 24px, bold);
}

.button-white:not(.button-white--loading) {
	&:not(:disabled):hover {
        background-color: #FF9514;
        color: #ffffff;
    }

    &:not(:disabled):active {
        opacity: 0.6;
    }

	&:disabled {
		opacity: 0.3;
	}
}

.button-orange--loading,
.button-white--loading {
	color: #FF9514;
	&::after {
		content: "";
		position: absolute;
		width: 21px;
		height: 21px;
		border: 2px solid #ffffff;
		border-radius: 50%;
		top: 50%;
		left: 50%;
		border-top-color: transparent;		
		animation: rotate 1s infinite;
	}
}

.button-white--loading {
	color: #ffffff;
	&::after {
		border-color: #FF9514;
		border-top-color: transparent;
	}
}

.overlay,
.menu-overlay {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.4);
	z-index: 8;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

@media (max-width: 768px) {
	.wrapper,
	.slide-wrapper {
		padding: 0 36px;
	}

	body {
		padding-top: 70px;
	}
}

@media (max-width: 660px) {
	.wrapper {
		padding: 0 20px;
	}

	.slide-wrapper {
		padding: 0;
		background-color: #000000;
	}
}