.menu {
    position: relative;
    display: flex;
    align-items: center;
    padding: 21px 0;
}

.menu__close {
    display: none;
    position: absolute;
    top: 33px;
    right: 40px;
    width: 24px;
    height: 24px;
    background-color: transparent;
    border: none;

    &::after,
    &::before {
        position: absolute;
        content: "";
        width: 22px;
        height: 2px;
        top: 12px;
        left: 0px;
        background-color: #111111;
    }

    &::before {
        transform: rotate(-45deg);
    }

    &::after {
        transform: rotate(45deg);
    }
}

.menu__toggle {
    display: none;
    position: relative;
    width: 22px;
    height: 17px;
    border: none;
    background-color: transparent;
    outline: none;

    &::after {
        position: absolute;
        content: "";
        width: 22px;
        height: 3px;
        top: 0;
        left: 0;
        background-color: #111111;
        box-shadow: 0 7px 0 0 #111111;
    }

    &::before {
        position: absolute;
        content: "";
        width: 16px;
        height: 3px;
        bottom: 0;
        left: 0;
        background-color: #111111;
    }
}

.menu__list {
    display: flex;
}

.menu__item {
    position: relative; 
    
    &:first-child:hover .menu__sub-menu {
        display: flex;
        z-index: 10;
    }
}

.menu__link {
    display: block;
    padding: 6px 16px;
    @include setFont(16px, 24px, bold);
    color: #575757;
    background-color: #ffffff;
    cursor: pointer;

    &:hover {
        background: #F3F3F4;
        border-radius: 8px;
    }
}

.menu__sub-menu {
    position: absolute;
    padding: 10px 0;
    width: 243px;
    top: 44px;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    background-color: #ffffff;
    border: 1px solid #F3F3F4;
    box-sizing: border-box;
    box-shadow: 0px 4px 4px rgba(51, 51, 51, 0.04), 0px 14px 24px rgba(51, 51, 51, 0.12);
    border-radius: 15px;

    &::before {
        content: "";
        position: absolute;
        top: -10px;
        left: 0;
        width: 100%;
        height: 10px;
        background-color: transparent;
    }
}

.menu__sub-item {
    width: 100%;
}

.menu__sub-link {
    display: block;
    padding: 8px 16px;
    font-weight: normal;
    text-align: left;
    color: #111111;

    &:hover {
        background-color: #F1F1F1;
    }
}

.menu__request {
    margin-left: 40px;    
}

@media (max-width: 960px) {
    .menu {
        display: none;
    }

    .menu__toggle {
        display: block;
    }
    .menu--opened {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        position: fixed;
        padding: 44px;
        top: 0;
        width: 330px;
        height: 100vh;
        right: 0;
        background-color: #ffffff;
        border-radius: 32px 0px 0px 0px;
        z-index: 10;

        & .menu__list {
            margin-top: 22px;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
        }

        & .menu__close {
            display: block;
        }
    }

    .menu__item {
        width: 100%;
    }

    .menu__link {
        @include setFont(24px, 24px, bold);
        margin-bottom: 32px;
    }

    .menu__request {
        margin: 0;
        width: 100%;

        background-color: #FF9514;
        color: #ffffff;

    
        &:hover {
            background-color: #111111;
            color: #ffffff;
        }
    
        &:active {
            opacity: 0.6;
        }
    }

    .menu__sub-menu {
        position: relative;
        top: -32px;
    }
}

@media (max-width: 660px) {
    .menu__toggle::after {
        background-color: #ffffff;
        box-shadow: 0 7px 0 0 #ffffff;
    }

    .menu__toggle::before {
        background-color: #ffffff;
    }
}