  margin-top: 32px;
  justify-content: space-between; }

.calc-inputs__field {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 427px; }
  .calc-inputs__field:not(:last-child) {
    margin-right: 32px; }
  .calc-inputs__field label {
    font-size: 16px;
    line-height: 20px;
    font-weight: normal;
    color: #575757; }
  .calc-inputs__field input:not(.calc-inputs__procent),
  .calc-inputs__field .calc-inputs__procent-calc {
    margin-top: 24px;
    width: 100%;
    padding: 16px 24px;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    font-size: 30px;
    line-height: 36px;
    font-weight: normal;
    border: none;
    background-color: #F3F3F4;
    border-radius: 16px;
    color: #575757;
    outline: none; }
  .calc-inputs__field input:focus {
    background-color: #fff; }
  .calc-inputs__field--disabled {
    opacity: 0.4; }
  .calc-inputs__field:nth-child(1)::after, .calc-inputs__field:nth-child(3)::after {
    position: absolute;
    position: absolute;
    bottom: 20px;
    right: 24px;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    font-size: 30px;
    line-height: 36px;
    font-weight: bold;
    color: #575757; }
  .calc-inputs__field:nth-child(1)::after {
    content: "\20BD"; }
  .calc-inputs__field:nth-child(3)::after {
    content: "мес."; }

.calc-inputs__procent-calc::after {
  position: absolute;
  content: "%";
  bottom: 23px;
  right: 20px;
  font-family: 'Nekst-Black', 'Arial', sans-serif;
  font-size: 20px;
  line-height: 22px;
  font-weight: bold; }

.calc-inputs__procent {
  position: absolute;
  bottom: 7px;
  right: 7px;
  width: 70px;
  padding: 15px 17px;
  border-radius: 16px;
  border: none;
  outline: none;
  font-family: 'Nekst-Black', 'Arial', sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: bold;
  color: #575757;
  background-color: #EBEBEC; }

.calc-inputs__range {
  width: 90%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%); }

.calc-inputs__line {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: transparent; }

.calc-inputs__line-move {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #FF9514; }

.calc-inputs__toggle {
  position: absolute;
  width: 20px;
  height: 20px;
  top: -8px;
  left: 120px;
  border-radius: 50%;
  background-color: #FF9514;
  border: none;
  outline: none; }

@media (max-width: 1024px) {
  .calc-inputs {
    flex-direction: column;
    align-items: center;
    width: 100%; }
  .calc-inputs__field {
    width: 100%;
    margin-top: 32px; }
    .calc-inputs__field:not(:last-child) {
      margin-right: 0; } }

@media (max-width: 660px) {
  .calc-inputs__field {
    margin-top: 25px; }
    .calc-inputs__field label {
      font-size: 14px;
      line-height: 18px;
      font-weight: normal; }
    .calc-inputs__field input {
      font-size: 22px;
      line-height: 20px;
      font-weight: normal; }
    .calc-inputs__field:nth-child(1)::after, .calc-inputs__field:nth-child(3)::after {
      font-size: 22px;
      line-height: 20px;
      font-weight: normal; }
    .calc-inputs__field input:not(.calc-inputs__procent),
    .calc-inputs__field .calc-inputs__procent-calc {
      margin-top: 8px; } }

.calc-result {
  margin-top: 44px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.calc-result__result {
  display: flex;
  justify-content: space-between;
  width: 66%; }

.calc-result__field {
  width: 427px;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }
  .calc-result__field:not(:last-child) {
    margin-right: 32px; }
  .calc-result__field span:first-child {
    font-size: 16px;
    line-height: 20px;
    font-weight: normal;
    color: #575757; }
  .calc-result__field span:last-child {
    margin-top: 8px;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    font-size: 54px;
    line-height: 48px;
    font-weight: normal;
    color: #575757; }

.calc-result__button {
  margin-top: 0;
  padding: 18px;
  width: 30%;
  font-family: 'Nekst-Black', 'Arial', sans-serif;
  font-size: 30px;
  line-height: 36px;
  font-weight: bold; }

@media (max-width: 1024px) {
  .calc-result__button {
    margin-top: 44px;
    width: 345px; }
  .calc-result__result {
    width: 100%; } }

@media (max-width: 660px) {
  .calc-result__result {
    flex-direction: column; }
  .calc-result__field {
    width: 100%;
    margin-bottom: 32px; }
    .calc-result__field:not(:last-child) {
      margin-right: 0; }
    .calc-result__field span:first-child {
      font-size: 14px;
      line-height: 19px;
      font-weight: normal; }
    .calc-result__field span:last-child {
      margin-top: 8px;
      font-size: 22px;
      line-height: 20px;
      font-weight: normal; }
  .calc-result__button {
    margin-top: 0;
    font-size: 22px;
    line-height: 20px;
    font-weight: normal; } }

.popup {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  z-index: 20;
  width: 100vw;
  border-radius: 32px 32px 0px 0px;
  min-width: 320px; }

.popup__container {
  padding-top: 160px;
  padding-bottom: 80px;
  width: 655px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start; }

.popup__title {
  font-family: 'Nekst-Black', 'Arial', sans-serif;
  font-size: 54px;
  line-height: 48px;
  font-weight: bold; }

.popup__text {
  max-width: 466px;
  margin-top: 24px;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
  color: #575757; }

.popup__form {
  margin-top: 44px; }

.popup__submit {
  width: 100%;
  margin-top: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 2px solid #F3F3F4;
  border-radius: 16px;
  padding: 30px 30px 30px 23px; }

.popup__agreement {
  max-width: 350px;
  font-size: 14px;
  line-height: 21px;
  font-weight: normal;
  color: #575757; }
  .popup__agreement a {
    color: #111111; }

.popup__btn {
  margin-top: 0;
  width: 174px; }

.popup__messengers {
  margin-top: 32px;
  width: 100%;
  display: flex;
  justify-content: center; }

.popup__messenger-link {
  display: block;
  width: 48px;
  height: 48px;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%; }

.popup__messenger-item:first-child {
  margin-right: 16px; }

.popup__messenger-item:first-child .popup__messenger-link {
  background-image: url("../img/whatsapp-icon.png"); }

.popup__messenger-item:last-child .popup__messenger-link {
  background-image: url("../img/telegram-icon.png"); }

.popup__close {
  position: absolute;
  top: 48px;
  right: 48px;
  width: 48px;
  height: 48px;
  background-color: #F3F3F4;
  border-radius: 50%;
  border: none;
  z-index: 11; }
  .popup__close::after, .popup__close::before {
    position: absolute;
    content: "";
    width: 22px;
    height: 2px;
    top: 23px;
    left: 13px;
    background-color: #828282; }
  .popup__close::before {
    transform: rotate(-45deg); }
  .popup__close::after {
    transform: rotate(45deg); }

@media (max-width: 660px) {
  .popup__container {
    width: auto;
    min-width: 280px;
    padding: 20px; }
  .popup__title {
    margin: 0;
    font-size: 22px;
    line-height: 20px;
    font-weight: bold; }
  .popup__text {
    margin-top: 16px;
    font-size: 14px;
    line-height: 19px;
    font-weight: normal; }
  .popup__close {
    transform: scale(0.5);
    top: 10px;
    right: 12px; }
  .popup__form {
    margin-top: 32px; }
  .popup__submit {
    padding: 0;
    margin: 0;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border: none; }
  .popup__agreement {
    order: 2;
    margin-top: 16px; }
  .popup__btn {
    order: 1;
    width: 100%; }
  .popup__messengers {
    margin-top: 24px;
    margin-bottom: 70px; } }

.form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; }

.form__fields {
  width: 100%;
  display: flex;
  justify-content: space-between; }

.form__field {
  position: relative;
  width: 312px;
  margin-right: 32px;
  background-color: #F3F3F4;
  border-radius: 16px;
  border: 1px solid transparent; }
  .form__field label {
    display: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 24px;
    font-size: 16px;
    line-height: 24px;
    font-weight: bold;
    color: #575757; }
  .form__field input {
    width: 100%;
    padding: 12px 24px 12px 24px;
    background-color: transparent;
    border: none;
    outline: none;
    border-radius: 16px;
    font-size: 16px;
    line-height: 24px;
    font-weight: bold;
    color: #575757; }
    .form__field input:focus {
      background-color: #ffffff; }

.form__field-message {
  display: none;
  position: absolute;
  bottom: -20px;
  left: 10px;
  color: #D53234;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal; }

.form__field--focus {
  border-color: #ff9514;
  box-shadow: 0 0 0 4px rgba(255, 149, 20, 0.2); }
  .form__field--focus input {
    box-sizing: border-box;
    border-radius: 16px;
    line-height: 16px;
    padding: 24px 24px 0 24px; }
  .form__field--focus label {
    display: block;
    top: 8px;
    transform: translateY(0);
    font-size: 12px;
    line-height: 12px;
    font-weight: normal; }

.form__field--invalid {
  border-color: #D53234; }

.form__field--valid::before {
  position: absolute;
  content: "";
  bottom: 12px;
  right: 12px;
  width: 24px;
  height: 24px;
  background-color: #77DE8D;
  border-radius: 8px; }

.form__field--valid::after {
  position: absolute;
  content: "";
  bottom: 22px;
  right: 18px;
  width: 10px;
  height: 5px;
  transform: rotate(-45deg);
  background-color: transparent;
  border: 2px solid #FFFFFF;
  border-top: none;
  border-right: none; }

.form__field--invalid .form__field-message {
  display: block; }

@media (max-width: 768px) {
  .form__fields {
    flex-direction: column; }
  .form__field {
    width: 100%;
    margin-right: 0;
    margin-bottom: 16px; } }
