.popup {
    display: none;
    position: fixed;
    bottom: 0;
    left: 0;
    background-color: #ffffff;
    z-index: 20;
    width: 100vw;
    border-radius: 32px 32px 0px 0px;
    min-width: 320px;
}

.popup__container {
    padding-top: 160px;
    padding-bottom: 80px;
    width: 655px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.popup__title {
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(54px, 48px, bold)
}

.popup__text {
    max-width: 466px;
    margin-top: 24px;
    @include setFont(16px, 24px, normal);
    color: #575757;
}

.popup__form {
    margin-top: 44px;
}

.popup__submit {
    width: 100%;
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 2px solid #F3F3F4;
    border-radius: 16px;
    padding: 30px 30px 30px 23px;
}

.popup__agreement {
    max-width: 350px;
    @include setFont(14px, 21px, normal);
    color: #575757;

    & a {
        color: #111111;
    }
}

.popup__btn {
    margin-top: 0;
    width: 174px;
}

.popup__messengers {
    margin-top: 32px;
    width: 100%;
    display: flex;
    justify-content: center;
}

.popup__messenger-link {
    display: block;
    width: 48px;
    height: 48px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50%;
}

.popup__messenger-item:first-child {
    margin-right: 16px;
}

.popup__messenger-item:first-child .popup__messenger-link {
    background-image: url('../img/whatsapp-icon.png');
}

.popup__messenger-item:last-child .popup__messenger-link {
    background-image: url('../img/telegram-icon.png');
}

.popup__close {
    position: absolute;
    top: 48px;
    right: 48px;
    width: 48px;
    height: 48px;
    background-color: #F3F3F4;
    border-radius: 50%;
    border: none;
    z-index: 11;

    &::after,
    &::before {
        position: absolute;
        content: "";
        width: 22px;
        height: 2px;
        top: 23px;
        left: 13px;
        background-color: #828282;
    }

    &::before {
        transform: rotate(-45deg);
    }

    &::after {
        transform: rotate(45deg);
    }
}

@media (max-width: 660px) {
    .popup__container {
        width: auto;
        min-width: 280px;
        padding: 20px;
    }

    .popup__title {
        margin: 0;
        @include setFont(22px, 20px, bold)
    }
    
    .popup__text {
        margin-top: 16px;
        @include setFont(14px, 19px, normal);
    }

    .popup__close {
        transform: scale(0.5);
        top: 10px;
        right: 12px;
    }

    .popup__form {
        margin-top: 32px;
    }

    .popup__submit {
        padding: 0;
        margin: 0;
        flex-direction: column;
        align-items: center;
        text-align: center;        
        border: none;
    }

    .popup__agreement {
        order: 2;
        margin-top: 16px;
    }

    .popup__btn {
        order: 1;
        width: 100%;
    }

    .popup__messengers {
        margin-top: 24px;
        margin-bottom: 70px;
    }
}