.calculator {
    padding-top: 100px;
    padding-bottom: 144px;
}

.calculator__title {
    margin: 0;
    max-width: 750px;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(54px, 54px, bold)
}

@media (max-width: 660px) {
    .calculator__title {
        @include setFont(34px, 30px, bold)
    }
}