.slider {
    position: relative;
    width: 100%;     
}

.slider__slides {
    position: relative;
    background-color: #000000;
    border-radius: 32px;
    overflow: hidden;
}

.slider__controls {
    position: absolute;
    bottom: 47px;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 5;
}

.slider__dots {
    display: flex;
    align-items: center;
}

.slider__dot {
    margin-right: 20px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #ffffff;
    opacity: 0.4;
    cursor: pointer;

    &--active {
        width: 12px;
        height: 12px;
        opacity: 1;
    }
}

.slider__buttons {
    position: absolute;
    right: 90px;
    bottom: 0;
}

.slider__button {
    position: relative;
    width: 24px;
    height: 24px;
    border: 2px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    background-color: transparent;
    outline: none;

    &::after {
        position: absolute;
        content: "";
        width: 5px;
        height: 5px;
        top: 6px;
        left: 7px;
        transform: rotate(-225deg);
        border: 2px solid rgba(255, 255, 255, 0.3);
        border-left: none;
        border-top: none;
    }

    &.slider__next {
        width: 48px;
        height: 48px;
        margin-left: 16px;
        border: none;
    }

    &.slider__next::after {
        width: 11px;
        height: 11px;
        top: 16px;
        left: 15px;
        transform: rotate(-45deg);
    }

    &:not(:disabled):hover {
        background-color: rgba(255, 255, 255, 0.2);
    }

    &:not(:disabled):active {
        background-color: rgba(255, 255, 255, 0.4);
    }

    &:disabled {
        opacity: 0.3;
    }

    &:not(:disabled):hover::after {
        border-color: rgba(255, 255, 255, 1);
    }
}

.slider__next svg {
    position: absolute;
    top: 0;
    left: 0;
}

.slider__btn-outline {
    opacity: 0.3;
    
}

.slider__moving-outline {
    transform: rotate(-90deg);
}

.slider__moving-outline circle {
    stroke-dashoffset: 150;
    stroke-dasharray: 150;
    transition: stroke-dashoffset 0.3s linear;
}

.slider__slide {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    height: 473px;
    padding: 70px;
    background-image: url("../img/slide-1.png");
    color: #ffffff;
    background-color: #000000;
    
    background-repeat: no-repeat;
    background-position: 500px 0;
    background-size: auto 100%;
}

.slider__slide--show {
    display: flex;
}

.slider__title,
.slider__text {
    font-family: 'Nekst-Black', 'Arial', sans-serif;
}

.slider__title {
    margin: 0;
    max-width: 585px;
    min-width: 260px;
    @include setFont(54px, 48px, bold)
}

.slider__text {
    margin-top: 32px;
    @include setFont(30px, 36px, bold)
}

.slider__request {
    margin-top: 24px;
}

@media (max-width: 1024px) {
    .slider__slide {
        background-position: 253px 90px;
        background-size: auto 75%;
    }
}

@media (max-width: 768px) {
    .slider__slide {
        background-position: 50% -25%;
        background-size: auto 60%;
        padding: 44px;
        justify-content: flex-end;
    }

    .slider__text {
        display: none;
    }

    .slider__controls {
        bottom: 27px;
    }

    .slider__request {
        margin-bottom: 35px;
    }

    .slider__buttons {
        display: none;
    }
}

@media (max-width: 660px) {
    .slider__title {
        margin: 0;
        @include setFont(32px, 28px, bold)
    }

    .slider__request {
        width: 174px;
        margin-bottom: 86px;
    }

    .slider__slide {
        padding: 20px;
        background-position: 100% -25%;
        background-size: auto 55%;
    }
}