.page-header {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    min-width: 320px;
    justify-content: space-between;
    background-color: #ffffff;    
    z-index: 7;
}

@media(min-width: 1440px) {
    .page-header {
        left: 50%;
        transform: translateX(-50%);
    }
}

@media (max-width: 960px) {
    .page-header {
        padding-top: 28px;
        padding-bottom: 24px;
    }
}

@media (max-width: 660px) {
    .page-header {
        padding-top: 20px;
        padding-bottom: 20px;
        background-color: #000000;
    }
}