.calc-result {
    margin-top: 44px;
    display: flex;
    justify-content: space-between;   
    flex-wrap: wrap;
}

.calc-result__result {
    display: flex;
    justify-content: space-between;
    width: 66%;
}

.calc-result__field {
    width: 427px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &:not(:last-child) {
        margin-right: 32px;
    }

    & span:first-child {
        @include setFont(16px, 20px, normal);
        color: #575757;
    }

    & span:last-child {
        margin-top: 8px;
        font-family: 'Nekst-Black', 'Arial', sans-serif;
        @include setFont(54px, 48px, normal);
        color: #575757;
    }
}

.calc-result__button {
    margin-top: 0;
    padding: 18px;
    width: 30%;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(30px, 36px, bold);
}

@media (max-width: 1024px) {
    .calc-result__button {
        margin-top: 44px; 
        width: 345px;       
    }

    .calc-result__result {
        width: 100%;
    }
}

@media (max-width: 660px) {
    .calc-result__result {
        flex-direction: column;
    }
    .calc-result__field {
        width: 100%;
        margin-bottom: 32px;
    
        &:not(:last-child) {
            margin-right: 0;
        }
    
        & span:first-child {
            @include setFont(14px, 19px, normal);
        }
    
        & span:last-child {
            margin-top: 8px;
            @include setFont(22px, 20px, normal);
        }
    }
    .calc-result__button {
        margin-top: 0;        
        @include setFont(22px, 20px, normal);
    }
}