.calc-inputs {
    display: flex;
    margin-top: 32px;
    justify-content: space-between;
}

.calc-inputs__field {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 427px;

    &:not(:last-child) {
        margin-right: 32px;
    }    

    & label {
        @include setFont(16px, 20px, normal);
        color: #575757;
    }

    & input:not(.calc-inputs__procent),
    .calc-inputs__procent-calc {
        margin-top: 24px;
        width: 100%;
        padding: 16px 24px;
        font-family: 'Nekst-Black', 'Arial', sans-serif;
        @include setFont(30px, 36px, normal);
        border: none;
        background-color: #F3F3F4;
        border-radius: 16px;
        color: #575757;
        outline: none;
    }

    & input:focus {
        background-color: #fff;
    }

    &--disabled {
        opacity: 0.4;
    }

    &:nth-child(1)::after,
    &:nth-child(3)::after {
        position: absolute;
        position: absolute;
        bottom: 20px;
        right: 24px;
        font-family: 'Nekst-Black', 'Arial', sans-serif;
        @include setFont(30px, 36px, bold);
        color: #575757;
    }
    &:nth-child(1)::after {
        content: "\20BD";
    }

    &:nth-child(3)::after {
        content: "мес.";
    }
}

.calc-inputs__procent-calc::after {
    position: absolute;
    content: "%";
    bottom: 23px;
    right: 20px;
    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(20px, 22px, bold);
}

.calc-inputs__procent {
    position: absolute;
    bottom: 7px;
    right: 7px;
    width: 70px;    
    padding: 15px 17px;
    
    border-radius: 16px;
    border: none;
    outline: none;

    font-family: 'Nekst-Black', 'Arial', sans-serif;
    @include setFont(20px, 24px, bold);
    color: #575757;
    background-color: #EBEBEC;
}

.calc-inputs__range {
    width: 90%;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.calc-inputs__line {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: transparent;
}

.calc-inputs__line-move {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #FF9514;
}

.calc-inputs__toggle {
    position: absolute;
    width: 20px;
    height: 20px;
    top: -8px;
    left: 120px;
    border-radius: 50%;
    background-color: #FF9514;
    border: none;
    outline: none;
}

@media (max-width: 1024px) {
    .calc-inputs {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .calc-inputs__field {
        width: 100%;
        margin-top: 32px;

        &:not(:last-child) {
            margin-right: 0;
        }
    }
}

@media (max-width: 660px) {
    .calc-inputs__field {
        margin-top: 25px;
        & label {
            @include setFont(14px, 18px, normal);
        }
    
        & input {
            @include setFont(22px, 20px, normal);
        }

        &:nth-child(1)::after,
        &:nth-child(3)::after {
            @include setFont(22px, 20px, normal);
        }

        & input:not(.calc-inputs__procent),
        .calc-inputs__procent-calc {
            margin-top: 8px;
        }
    }
    
}